// @ts-ignore
import React, { Fragment } from "react";
import { nanoid } from "nanoid";
import * as stylex from "@stylexjs/stylex";
import "./Tokens.stylex";
import { colors, sizes, fonts } from "./Tokens.stylex";
const styles = {
  button: {
    cursor: "x1ypdohk",
    fontSize: "x1onedhc",
    lineHeight: "x9i4maa",
    padding: "x1s2q3x9",
    display: "x78zum5",
    gap: "x11w6kqi",
    alignItems: "x6s0dn4",
    justifyContent: "x1nhvcw1",
    border: "x1wty727",
    background: "x11g6tue",
    borderRadius: "x2u8bby",
    color: "x1awj2ng",
    width: "xh8yej3",
    $$css: true
  },
  buttonHighlighted: {
    backgroundColor: "xiejsdu",
    $$css: true
  }
};
export default function OptionPreview({
  option,
  onClick,
  isHighlighted,
  onMouseEnter
}: {
  option: {
    label: string;
    value: string;
    colors: string[];
  };
  onClick: () => void;
  isHighlighted: boolean;
  onMouseEnter: () => void;
}) {
  const {
    colors,
    label
  } = option;
  const length = colors.length;
  const stop = 100 / length;
  const id = "bg-" + nanoid();
  return <button {...stylex.props(styles.button, isHighlighted && styles.buttonHighlighted)} onClick={onClick} onMouseEnter={onMouseEnter} type="button">
            <svg {...{
      className: "x1lliihq x15635tz xm73b7n x8cb4t4 x1yxg6fv x47corl"
    }} height="100" width="100" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" preserveAspectRatio="none">
                <defs>
                    <linearGradient x1="0%" y1="100%" x2="100%" y2="100%" id={id}>
                        {colors.map((color, index) => {
            const start = stop * index + "%";
            const end = stop * (index + 1) + "%";
            return <Fragment key={index}>
                                    <stop stopColor={color} offset={start}></stop>
                                    <stop stopColor={color} offset={end}></stop>
                                </Fragment>;
          })}
                    </linearGradient>
                </defs>
                <rect fill={`url(#${id})`} x="0" y="0" width="100" height="100"></rect>
            </svg>
            <span {...{
      className: "xlyipyv xuxw1ft xb3r6kr x98rzlu xdpxx8g"
    }} title={label}>
                {label}
            </span>
        </button>;
}