// @ts-ignore
import React from "react";
import { SelectBox } from "@neos-project/react-ui-components";
import { createSelectBoxOptions, colorForegroundAndBackgroundValues } from "../Utlis";
import * as stylex from "@stylexjs/stylex";
import OptionPreview from "../OptionPreview";
import "../Tokens.stylex";
import { colors, sizes } from "../Tokens.stylex";
import type { ColorObject } from "../Utlis";
const styles = {
  colorList: {
    position: "x1n2onr6",
    display: "x78zum5",
    gap: "x195vfkc",
    borderRadius: "x1dwtru6",
    $$css: true
  },
  colorListItem: {
    height: "xpdco3j",
    cursor: "x1ypdohk",
    border: "x1wty727",
    padding: "x1717udv",
    flex: "x98rzlu",
    ":first-child_borderTopLeftRadius": "xpryen9",
    ":first-child_borderBottomLeftRadius": "xqgiiig",
    ":last-child_borderTopRightRadius": "xn9o1y4",
    ":last-child_borderBottomRightRadius": "x192clpw",
    ":hover_boxShadow": "x1kstqak",
    ":hover_zIndex": "x1h71102",
    ":focus_boxShadow": "xovzxhn",
    ":focus_zIndex": "x11hdtt0",
    $$css: true
  },
  colorListItemActive: {
    boxShadow: "x1dl3sty",
    zIndex: "x1vjfegm",
    $$css: true
  },
  marginTop: {
    marginTop: "xyxxu3c",
    $$css: true
  },
  paddingBottom: {
    paddingBottom: "x1j43vul",
    $$css: true
  }
};
export default function Panel({
  foregroundColors,
  backgroundColors,
  minimalStrenghtSpan,
  i18n,
  state,
  setState,
  id,
  inPopover = false
}: {
  foregroundColors: ColorObject;
  backgroundColors: ColorObject;
  minimalStrenghtSpan: number;
  i18n: {
    [key: string]: string;
  };
  state: any;
  setState: Function;
  id: string;
  inPopover?: boolean;
}) {
  const selectBoxOptions = createSelectBoxOptions(backgroundColors, minimalStrenghtSpan);
  const colors: [ColorObject, ColorObject] = [backgroundColors, foregroundColors];
  function handleColorClick(newStrength: number, isBackground = false) {
    let otherStrength = state?.strength?.[isBackground ? 1 : 0];
    if (!Number.isInteger(otherStrength) || Math.abs(newStrength - otherStrength) < minimalStrenghtSpan) {
      otherStrength = null;
    }
    const strength: [number | null, number | null] = isBackground ? [newStrength, otherStrength] : [otherStrength, newStrength];
    setState(colorForegroundAndBackgroundValues({
      group: state.group,
      strength: strength,
      colors
    }));
  }
  function handleGroupChange(group: string) {
    const strength = state?.strength || null;
    setState(colorForegroundAndBackgroundValues({
      group,
      strength,
      colors
    }));
  }
  const bgColorsArray = createColorsArray(backgroundColors, state?.group, i18n);
  const fgColorsArray = createColorsArray(foregroundColors, state?.group, i18n).reverse();
  return <>
            <SelectBox {...stylex.props(inPopover && styles.marginTop)} id={id} options={selectBoxOptions} value={state?.group} placeholder={i18n.selectColorGroup} allowEmpty={false} onValueChange={handleGroupChange} ListPreviewElement={OptionPreview} />
            <div {...stylex.props(styles.colorList, inPopover && styles.paddingBottom)}>
                {bgColorsArray.map((entry: any) => {
        const current = entry.strength === state.strength?.[0];

        // Check if a foreground color is even possible
        if (entry.strength < minimalStrenghtSpan && entry.strength + minimalStrenghtSpan > 1000) {
          return;
        }
        return <button {...stylex.props(styles.colorListItem, current && styles.colorListItemActive)} key={state.group + entry.strength} style={{
          backgroundColor: entry.color
        }} title={entry.label} onClick={() => handleColorClick(entry.strength, true)}></button>;
      })}
            </div>
            {typeof state?.strength?.[0] == "number" && <div {...stylex.props(styles.colorList, inPopover && styles.paddingBottom)}>
                    {fgColorsArray.map((entry: any) => {
        const bgStrength = state.strength[0];
        const current = entry.strength === state.strength?.[1];

        // Test if the numbers are not to close to each other
        if (Math.abs(entry.strength - bgStrength) < minimalStrenghtSpan) {
          return;
        }
        return <button {...stylex.props(styles.colorListItem, current && styles.colorListItemActive)} key={state.group + entry.strength} style={{
          backgroundColor: entry.color
        }} title={entry.label} onClick={() => handleColorClick(entry.strength, false)}></button>;
      })}
                </div>}
        </>;
}
function createColorsArray(colors: ColorObject, group: string | null | undefined, i18n: {
  [key: string]: string;
}) {
  if (!group || !colors[group]) {
    return [];
  }
  const groupValues = colors[group];
  const values = [];
  for (const strength in groupValues) {
    const color = groupValues[strength];
    let label = strength;
    if (label == "0") {
      label = i18n.white;
    } else if (label == "1000") {
      label = i18n.black;
    }
    values.push({
      strength: typeof strength == "string" ? parseInt(strength) : strength,
      label,
      color
    });
  }
  return values;
}