// @ts-ignore
import React, { useState, useEffect, MouseEvent } from "react";
import { neos } from "@neos-project/neos-ui-decorators";
import { IconButton, Icon } from "@neos-project/react-ui-components";
import Panel from "./Panel";
import PreviewBox from "./PreviewBox";
import Error from "../Error";
import { generateColorObject, colorReturnValues } from "../Utlis";
import * as stylex from "@stylexjs/stylex";
import "../Tokens.stylex";
import { colors, sizes, transitions } from "../Tokens.stylex";
import type { StateObject } from "../Utlis";
const neosifier = neos(globalRegistry => ({
  i18nRegistry: globalRegistry.get("i18n"),
  config: globalRegistry.get("frontendConfiguration").get("CarbonTailwindColors")
}));
const defaultProps = {
  options: {
    allowEmpty: false,
    disabled: false,
    placeholder: null,
    scheme: null,
    collapsed: false,
    disableStrength: []
  }
};
const styles = {
  wrapper: {
    display: "x78zum5",
    flexDirection: "xdt5ytf",
    gap: "xoat92z",
    $$css: true
  },
  disabled: {
    cursor: "x1h6gzvc",
    opacity: "x190dgpg",
    ":where(*) *_pointerEvents": "x1g8rrie",
    $$css: true
  },
  highlight: {
    borderRadius: "x1dwtru6",
    boxShadow: "xypt998",
    $$css: true
  },
  noGap: {
    gap: "xxhr3t",
    rowGap: null,
    columnGap: null,
    $$css: true
  },
  popoverButton: {
    display: "x78zum5",
    alignItems: "x6s0dn4",
    justifyContent: "x1szn6h9",
    border: "x1gs6z28",
    padding: "x1717udv",
    minHeight: "x1q6vwdj",
    borderRadius: "x1dwtru6",
    cursor: "x1ypdohk",
    background: "x11g6tue",
    backgroundColor: "x1r3k6u3",
    $$css: true
  },
  popoverButtonIcon: open => [{
    transform: "x1v0jg1i",
    transition: "x1akcpmf",
    transitionBehavior: null,
    transitionDelay: null,
    transitionDuration: null,
    transitionProperty: null,
    transitionTimingFunction: null,
    minWidth: "x1fyu0iq",
    $$css: true
  }, {
    "--transform": (open ? "rotate(180deg)" : "rotate(0deg)") != null ? open ? "rotate(180deg)" : "rotate(0deg)" : "initial"
  }],
  popoverPanel: open => [{
    display: "xrvj5dj",
    gridTemplateRows: "xi7raoi",
    opacity: "x1u4uod0",
    transition: "x5z8be9",
    transitionBehavior: null,
    transitionDelay: null,
    transitionDuration: null,
    transitionProperty: null,
    transitionTimingFunction: null,
    $$css: true
  }, {
    "--gridTemplateRows": (open ? "1fr" : "0fr") != null ? open ? "1fr" : "0fr" : "initial",
    "--opacity": (open ? 1 : 0) != null ? open ? 1 : 0 : "initial",
    "--transition": `grid-template-rows ${transitions.default} ${transitions.timing}, opacity ${transitions.fast} ${transitions.timing} ${open ? transitions.fast : "0s"}` != null ? `grid-template-rows ${transitions.default} ${transitions.timing}, opacity ${transitions.fast} ${transitions.timing} ${open ? transitions.fast : "0s"}` : "initial"
  }]
};

// @ts-ignore
function Editor(props) {
  const options = {
    ...defaultProps.options,
    ...props.config,
    ...props.options
  };
  const {
    value,
    commit,
    highlight,
    i18nRegistry,
    id
  } = props;
  const colors = generateColorObject({
    colors: options.colors,
    scheme: options.scheme,
    grayscale: options.grayscale,
    disableStrength: options.disableStrength
  });
  if (!colors) {
    return <Error>{i18nRegistry.translate("Carbon.TailwindColors:Main:noColorsDefined")}</Error>;
  }
  const [state, setState]: [StateObject, any] = useState(colorReturnValues({
    group: value?.group,
    strength: value?.strength,
    colors
  }));
  useEffect(() => {
    if (!state?.hex) {
      if (value != "") {
        commit("");
      }
      return;
    }
    if (JSON.stringify(value) != JSON.stringify(state)) {
      commit(state);
    }
  }, [state]);
  const {
    allowEmpty,
    disabled,
    placeholder,
    collapsed
  } = options;
  const i18n = {
    white: i18nRegistry.translate("Carbon.TailwindColors:Main:white"),
    black: i18nRegistry.translate("Carbon.TailwindColors:Main:black"),
    selectColor: i18nRegistry.translate("Carbon.TailwindColors:Main:selectColor"),
    resetColor: i18nRegistry.translate("Carbon.TailwindColors:Main:resetColor"),
    selectColorGroup: i18nRegistry.translate("Carbon.TailwindColors:Main:selectColorGroup")
  };
  if (!collapsed) {
    return <div {...stylex.props(styles.wrapper, disabled && styles.disabled)}>
                {allowEmpty ? <div {...{
        className: "x78zum5 xoat92z"
      }}>
                        <PreviewBox placeholder={placeholder} state={state} colors={colors} i18n={i18n} highlight={highlight} />
                        <IconButton style="lighter" icon="times" title={i18n.resetColor} onClick={() => {
          setState(null);
        }} />
                    </div> : <PreviewBox placeholder={placeholder} state={state} colors={colors} i18n={i18n} highlight={highlight} />}
                <Panel colors={colors} i18n={i18n} state={state} setState={setState} id={id} />
            </div>;
  }
  const [open, setOpen] = useState(false);
  return <div {...stylex.props(styles.wrapper, options.disabled && styles.disabled, styles.noGap)}>
            <button {...stylex.props(styles.popoverButton, highlight && styles.highlight)} type="button" title={i18nRegistry.translate(`Carbon.ColorPicker.OKLCH:Main:${open ? "close" : "open"}Panel`)} onClick={() => setOpen(!open)} aria-expanded={open} aria-controls={`${id}-panel`}>
                <PreviewBox placeholder={placeholder} state={state} colors={colors} i18n={i18n} inPopover={true} />
                {Boolean(allowEmpty) && Boolean(state?.hex) && <IconButton style="light" icon="times" title={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:resetColor")} onClick={(event: MouseEvent<HTMLButtonElement>) => {
        event.stopPropagation();
        setState(null);
      }} />}
                <Icon icon="chevron-down" {...stylex.props(styles.popoverButtonIcon(open))} />
            </button>
            <div {...stylex.props(styles.popoverPanel(open))} aria-hidden={!open} id={`${id}-panel`}>
                <div {...{
        className: "x78zum5 xdt5ytf xoat92z xb3r6kr x1hr3lfm xlutiju"
      }}>
                    <Panel colors={colors} i18n={i18n} state={state} setState={setState} id={id} inPopover={true} />
                </div>
            </div>
        </div>;
}
export default neosifier(Editor);