// @ts-ignore
import React from "react";
import { SelectBox } from "@neos-project/react-ui-components";
import { createSelectBoxOptions, colorReturnValues, capitalizeFirstLetter } from "../Utlis";
import * as stylex from "@stylexjs/stylex";
import OptionPreview from "../OptionPreview";
import "../Tokens.stylex";
import { colors, sizes } from "../Tokens.stylex";
const styles = {
  colorList: {
    position: "x1n2onr6",
    display: "x78zum5",
    gap: "x195vfkc",
    borderRadius: "x1dwtru6",
    $$css: true
  },
  colorListItem: {
    height: "xpdco3j",
    cursor: "x1ypdohk",
    border: "x1wty727",
    padding: "x1717udv",
    flex: "x98rzlu",
    ":first-child_borderTopLeftRadius": "xpryen9",
    ":first-child_borderBottomLeftRadius": "xqgiiig",
    ":last-child_borderTopRightRadius": "xn9o1y4",
    ":last-child_borderBottomRightRadius": "x192clpw",
    ":hover_boxShadow": "x1kstqak",
    ":hover_zIndex": "x1h71102",
    ":focus_boxShadow": "xovzxhn",
    ":focus_zIndex": "x11hdtt0",
    $$css: true
  },
  colorListItemActive: {
    boxShadow: "x1dl3sty",
    zIndex: "x1vjfegm",
    $$css: true
  },
  marginTop: {
    marginTop: "xyxxu3c",
    $$css: true
  },
  paddingBottom: {
    paddingBottom: "x1j43vul",
    $$css: true
  }
};
export default function Panel({
  colors,
  i18n,
  state,
  setState,
  id,
  inPopover = false
}: {
  colors: any;
  i18n: {
    [key: string]: string;
  };
  state: any;
  setState: Function;
  id: string;
  inPopover?: boolean;
}) {
  const selectBoxOptions = createSelectBoxOptions(colors);
  function handleColorClick(group: string, strength: number) {
    setState(colorReturnValues({
      group,
      strength,
      colors
    }));
  }
  function handleGroupChange(group: string) {
    const strength = state?.strength || null;
    setState(colorReturnValues({
      group,
      strength,
      colors
    }));
  }
  const colorsArray = [];
  for (const group in colors) {
    const groupValues = colors[group];
    const label = capitalizeFirstLetter(group);
    const values = [];
    for (const strength in groupValues) {
      const color = groupValues[strength];
      let label = strength;
      if (label == "0") {
        label = i18n.white;
      } else if (label == "1000") {
        label = i18n.black;
      }
      values.push({
        strength: typeof strength == "string" ? parseInt(strength) : strength,
        label,
        color
      });
    }
    colorsArray.push({
      group,
      label,
      values
    });
  }
  return <>
            <SelectBox {...stylex.props(inPopover && styles.marginTop)} id={id} options={selectBoxOptions} value={state?.group} placeholder={i18n.selectColorGroup} allowEmpty={false} onValueChange={handleGroupChange} ListPreviewElement={OptionPreview} />
            {colorsArray.map(item => {
      const isActiveGroup = state && state.group == item.group;
      return isActiveGroup && <div {...stylex.props(styles.colorList, inPopover && styles.paddingBottom)}>
                            {item.values.map(entry => {
          const current = entry.strength == state.strength;
          return <button {...stylex.props(styles.colorListItem, current && styles.colorListItemActive)} key={item.group + entry.strength} style={{
            backgroundColor: entry.color
          }} title={entry.label} onClick={() => handleColorClick(item.group, entry.strength)}></button>;
        })}
                        </div>;
    })}
        </>;
}