// @ts-ignore
import React from "react";
import { capitalizeFirstLetter, getPreviewBoxColor } from "../Utlis";
import * as stylex from "@stylexjs/stylex";
import "../Tokens.stylex";
import { colors, sizes, fonts } from "../Tokens.stylex";
const styles = {
  previewBox: {
    flex: "x98rzlu",
    height: "x1nv6co2",
    lineHeight: "x1pl0vzw",
    border: "xq15zi8",
    borderRadius: "x1dwtru6",
    display: "x78zum5",
    alignItems: "x1pha0wt",
    paddingInline: "x7ks0zz",
    gap: "xoat92z",
    overflow: "xb3r6kr",
    textEllipsis: "x1sect9i",
    $$css: true
  },
  checkboard: {
    backgroundImage: "x1eyyrmo",
    backgroundSize: "x1ht9rhz",
    $$css: true
  },
  highlight: {
    borderRadius: "x1dwtru6",
    boxShadow: "xypt998",
    $$css: true
  },
  rightNotRounded: {
    borderTopRightRadius: "x1a2w583",
    borderBottomRightRadius: "xjppbhk",
    $$css: true
  }
};
export default function PreviewBox({
  state,
  foregroundColors,
  backgroundColors,
  i18n,
  highlight = false,
  inPopover = false
}: {
  state: any;
  foregroundColors: any;
  backgroundColors: any;
  i18n: any;
  highlight?: Boolean;
  inPopover?: Boolean;
}) {
  const group = state?.group ? capitalizeFirstLetter(state.group) : null;
  const bgIsWhite = state?.strength?.[0] === 0;
  const bgIsBlack = state?.strength?.[0] === 1000;
  const fgIsWhite = state?.strength?.[1] === 0;
  const fgIsBlack = state?.strength?.[1] === 1000;
  const output = [];
  if (Number.isInteger(state?.strength?.[0]) && Number.isInteger(state?.strength?.[1])) {
    if (bgIsWhite || bgIsBlack) {
      output.push(bgIsWhite ? i18n.white : i18n.black);
    } else {
      output.push(group);
      output.push(state.strength[0]);
    }
    output.push("/");
    if (fgIsWhite || fgIsBlack) {
      output.push(fgIsWhite ? i18n.white : i18n.black);
    } else {
      if (!output.includes(group)) {
        output.push(group);
      }
      output.push(state.strength[1]);
    }
  }
  return <div {...stylex.props(styles.previewBox, !state?.hex?.[0] && styles.checkboard, !inPopover && highlight && styles.highlight, inPopover && styles.rightNotRounded)} style={{
    backgroundColor: getPreviewBoxColor({
      colors: backgroundColors,
      group: state?.group,
      strength: state?.strength?.[0]
    }),
    color: getPreviewBoxColor({
      colors: foregroundColors,
      group: state?.group,
      strength: state?.strength?.[1]
    })
  }}>
            {output.map(item => {
      if (typeof item === "number") {
        return <span {...{
          className: "xamkqhe"
        }}>{item}</span>;
      }
      return <span>{item}</span>;
    })}
        </div>;
}